//BREAKPOINTS
@media (min-width: 64em) {
}

//CSS MAP - 640px, 1024px, 1400px
$breakpoints-up: (
  "medium": "40em",
  "large": "64em",
  "xlarge": "87.5em",
);

//CSS MAP - 639px, 1023px, 1399px
$breakpoints-down: (
  "small": "39.937em",
  "medium": "63.937em",
  "large": "87.437em",
);

@mixin breakpoint-up($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
